.sidebar {
    width: 317px;
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 102;
    padding: 16px 24px 16px 70px;
    transform: translateX(-100%);
    transition: 0.3s all ease;
}

.sidebar.open {
    transform: translateX(0);
}

.background {
    opacity: 0.3;
    background: #000;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 101;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
}

.nav a {
    color: #000;
    font-family: 'Montserrat';
    font-size: 22px;
    font-weight: 600;
    text-decoration: none;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: red;
}

.close {
    background: none;
    border: none;
    cursor: pointer;
    margin-bottom: 23px;
    margin-left: 200px;
}

